import { immerable } from "immer";
import {
  NAME_SEPARATOR,
  ObjectTypes,
  Region,
  RegionGridType,
  RegionShape,
} from "./Editor";
import { DeepPartial } from "ts-essentials";
import { buildPoint } from "./Point";
import { merge } from "lodash";
import * as THREE from "three";

export enum RegionChildSuffix {
  MAIN = "main",
  BG = "bg",
  GRID_CELLS = "GRID_CELLS",
  GRID_CELL = "gridCell",
  CONTENT = "content",
}

export const getThreeNameFromRegionId = (
  id: string,
  childSuffix: RegionChildSuffix = RegionChildSuffix.MAIN
) => {
  return `${ObjectTypes.Region}${NAME_SEPARATOR}${id}${NAME_SEPARATOR}${childSuffix}`;
};

export const getRegionIdFromThreeName = (name?: string) => {
  if (!name) {
    return undefined;
  }
  if (name.includes(ObjectTypes.Region) === false) {
    return undefined;
  }
  return name.split(NAME_SEPARATOR)[1];
};

export const getThreeUserDataFromRegion = (
  region: Region,
  childSuffix?: RegionChildSuffix
) => {
  return merge({}, region, {
    name: getThreeNameFromRegionId(region.id, childSuffix),
  });
};

export const insideRegionTriange = (
  region: Region,
  _row: number,
  column: number
) => {
  const row = region.rows - _row - 1;
  if (row >= region.rows || column < 0 || column >= region.columns) {
    return false;
  }
  const min = region.columns % 2 === 0 ? 2 : 1;
  const ratio = Math.max(0, row) / (region.rows - 1);
  let beadsCountInRow =
    min +
    Math.min(Math.floor((region.columns - min) * ratio), region.columns - min);
  if (region.columns % 2 != beadsCountInRow % 2) {
    beadsCountInRow++;
  }
  const emptyCells = region.columns - beadsCountInRow;
  const offsetFromStart = Math.floor(emptyCells / 2);
  const offsetFromEnd = beadsCountInRow + offsetFromStart;

  let result = true;
  if (column < offsetFromStart) {
    result = false;
  } else if (column >= offsetFromEnd) {
    result = false;
  }
  return result;
};

export const isActiveRegionCell = (region: Region, index: number) => {
  if (region.shape === RegionShape.Rectangle) {
    const newSize = region.rows * region.columns;
    if (index >= 0 && index < newSize) {
      return true;
    }
    return false;
  } else if (region.shape === RegionShape.Triangle) {
    const row = Math.floor(index / region.columns);
    const column = Math.floor(index % region.columns);
    const isInside = insideRegionTriange(region, row, column);
    return isInside;
  } else {
    console.error("region shape not handled", region.shape);
    return false;
  }
};

export const buildRegion = (props: DeepPartial<Region>): Region => {
  const defaults: Region = {
    id: THREE.MathUtils.generateUUID(),
    position: buildPoint(0, 0),
    gridType: RegionGridType.Rectangles,
    shape: RegionShape.Rectangle,
    height: 10,
    width: 10,
    beadIds: [],
    opacity: 0.5,
    columns: 3,
    rows: 3,
    autoFillBeads: false,
    snapBeadsToCell: true,
    canLockOrUnlock: true,
    isLocked: false,
    objectType: ObjectTypes.Region,
    childrenGridIds: [],
    depth: 1,
    rotateX: 0,
    rotateY: 0,
    rotateZ: 0,
  };
  const newRegion = merge(defaults, props);
  const size = newRegion.rows * newRegion.columns;
  const newChildrenGridIds = Array(size).fill(undefined);
  newRegion.childrenGridIds = merge(newChildrenGridIds, props.childrenGridIds);

  return newRegion;
};
